import Vue from "vue";
import store from "./vuex/store";
import VueRouter from "vue-router";
import routes from "./routers"; // 
import VueLazyload from "vue-lazyload"; // 
import "common/css/index.styl"; // 
import common from "./common/js/common.js";
import sharejs from "./common/js/sharejs.js";
import "../static/js/rem.js";
import axios from "./common/js/http.js";
import { savaToLocal, loadFromlLocal } from "./common/js/storage.js"; // 

var firstRouter = true; //index

// 
Vue.use(common);
Vue.use(VueRouter);
const router = new VueRouter({
  mode: "hash",
  linkActiveClass: "active", // 
  routes //  routes: routes
});
axios.globalRouter = router;
Vue.prototype.$http = axios;

// errorloading, require attempt
Vue.use(VueLazyload, {
  error: require("./assets/404.png"),
  loading: require("./assets/loading.gif"),
  attempt: 1
});

//webpackprocess.env.NODE_ENVproductionprocess.env.NODE_ENV
if (process.env.NODE_ENV != "production") {
  // savaToLocal("aml", "openid", "ee862dbd3806f661eaf0e5311e7cce56");
  // savaToLocal("aml", "userid", "317");
  // savaToLocal("aml", "token", "216cb96cc14fa4bdaa84459cf2e6bc41");
}

// token
let openid = loadFromlLocal("aml", "openid", "");
let userid = loadFromlLocal("aml", "userid", "0");
let token = loadFromlLocal("aml", "token", "");
if (!openid || userid == "0") token = "";

store.commit("UPDATE_OPNEID", openid);
store.commit("UPDATE_USERID", userid);
store.commit("UPDATA_TOKEN", token);

// 
router.beforeEach((to, from, next) => {
  //index
  if (firstRouter) {
    store.dispatch("toggleLoading", true);
    firstRouter = false;
    next({ path: "/", replace: true });
    return;
  }

  if (to.matched.some(r => r.meta.requireAuth && !store.state.token)) {
    next({ path: "/login", query: { redirect: to.fullPath } });
    return;
  }
  // params
  // storeparams
  if (Object.keys(to.params).length == 0) {
    Object.assign(to.params, store.state.paramMap[to.name] || {});
  }
  store.commit("REFRESHROUTERPARAM", { key: to.name, value: to.params }); // urlparams
  store.dispatch("toggleLoading", true);
  next();
});

router.afterEach((to, from, next) => {
  //
  if (to.params && to.params.titleName) {
    titleName = to.params.titleName;
    store.commit("UPDATE_TITLE", { title: to.params.titleName, show: true });
  } else {
    if (to.meta.title)
      store.commit("UPDATE_TITLE", {
        title: to.meta.title,
        show: true
      });
    else store.commit("UPDATE_TITLE", "", { title: "", show: false });
  }
  //
  if (to.meta && to.meta.bodyBgColor) {
    document.body.style.backgroundColor = to.meta.bodyBgColor;
  }
});
/**
 * 
 *  router 
 * 
 */
const routerApp = new Vue({
  store,
  router
}).$mount("#app");

// window.setCookie("amxl_detailsid", "37");

sharejs.defultShareMsg();
sharejs.getWxData();
//
function PayToRouter() {
  var buystate = window.getCookie("buyState");
  if (buystate != "paycode") return false;

  var payTypes = "";
  var payMsg = window.getCookie("payMsg");
  if (payMsg != "" && payMsg != null) {
    var payArr = payMsg.split(",");
    for (var i = 0; i < payArr.length; i++) {
      var parr = payArr[i].split(":");
      if (parr[0] == "types") {
        payTypes = parr[1];
        break;
      }
    }
  }
  if (payTypes == "GoodsPayParam") {
    //
    router.replace("/payAffirm");
    window.setCookie("buyState", "waitpayToServer");
  } else if (payTypes == "DepositPayParam") {
    //
    router.replace("/pledgeAffirm");
    window.setCookie("buyState", "waitpayToServer");
  }
  return true;
}
//,
//
//
if (token) {
  Vue.prototype.updatateUserInfo(() => {
    judgeWhereToJump();
  });
} else judgeWhereToJump();

function judgeWhereToJump() {
  if (!PayToRouter()) {
    router.replace({ name: "home" });
    window.setCookie("buyState", "");
  }
}

function getUrlStr(name) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
  var r = window.location.search.substr(1).match(reg);
  if (r != null) return unescape(r[2]);
  return null;
}

export default routerApp;



